* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
.App {
  position: relative;
}
.Home {
  width: 100%;
  min-height: 100vh;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;  
}
.Home::before {
  content: '';
  background: url('./components/images/black.jpg') no-repeat;
  background-size: cover;
  background-position: center;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
.Home .container {
  width: 100%;
  height: 100%;
  display: grid;
  grid-template-columns: repeat(8, minmax(200px, 1fr));
}